p {
  font-size: 10px;
  font-weight: bold;
  margin-top: 0;
  border-top: 0;
  padding-top: 0;
  background-color: white;
}
p em {
  background: yellow;
  padding: 5px;
}
.p {
  position: fixed;
  width: 100%;
}
#table-wrapper {
  overflow: inherit;
}
.lg {
  font-size: 18px;
  font-weight: normal;
}
.sortable-column-label {
  float: left;
}
.sortable-column-symbol {
  float: right;
  padding-right: 10px;
}
td,
th {
  text-align: left;
}
input:focus {
  outline: none!important;
}
.button {
  border: none;
  color: black;
  padding: 12px 28px;
  text-decoration: none;
  margin-right: 4px;
  background-color: #929497;
  display: inline-block;
  font-size: 16px;
}
button:active {
  background-color: #4CAF80;
}
.inputField {
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 4px;
  box-sizing: border-box;
  height: 40px;
  width: 150px;
}
.table-hover > tbody > tr:hover > td,
.table-hover > tbody > tr:hover > th {
  background-color: #c3dfff;
}
h5 {
  resize: horizontal;
  overflow: hidden;
  height: 120px;
  margin: 0px;
  padding: 0px;
  border: 1px solid black;
  display: block;
}
th h5 {
  border: 0;
  height: auto;
  min-height: 20px;
}
.form-select {
  border: none;
  padding: 5px 20px;
  width: 25%;
  height: 15%;
  text-decoration: none;
  margin-right: 4px;
  display: inline-block;
  font-size: 14px;
}
option {
  border: none;
  padding: 10px 28px;
  width: 15%;
  text-decoration: none;
  margin-right: 4px;
  display: inline-block;
  font-size: 12px;
}
#questMark {
  margin-right: 50px;
  margin-left: 30px;
}
